import { ReactBnbGallery } from 'react-bnb-gallery'
import 'react-bnb-gallery/dist/style.css'
import '../styles/gallery.styl'

export default function Carousel({
  work,
  activeImageIndex,
  setActiveImageIndex,
  galleryOpen,
  setGalleryOpen,
  segment
}) {
  return (
    <ReactBnbGallery
    show={galleryOpen}
    preloadSize={1}
    activePhotoIndex={activeImageIndex}
    photos={work.map(({ gallery }) => gallery)}
    onClose={() => {
      // @ebaker - quick hack to remove double bars on small device height
      document.body.style.removeProperty('overflow-y')
      segment().track('Click Gallery Close', {gallery: work[activeImageIndex].gallery})
      setGalleryOpen(false)
    }}
    nextButtonPressed={() => {
      segment().track('Click Gallery Next', {gallery: work[activeImageIndex].gallery})
      const nextIndex = activeImageIndex + 1 >= work.length ? 0 : activeImageIndex + 1
      setActiveImageIndex(nextIndex)
    }}
    prevButtonPressed={() => {
      segment().track('Click Gallery Prev', {gallery: work[activeImageIndex].gallery})
      const prevIndex =
        activeImageIndex - 1 < 0 ? work.length - 1 : activeImageIndex - 1
      setActiveImageIndex(prevIndex)
    }}
    activePhotoPressed={moveNext => {
      const gallery = work[activeImageIndex].gallery
      const link = gallery.link
      if (link) {
        segment().track(`Gallery Link Clicked - ${gallery.caption}`, {gallery: gallery})
        window.open(link)
      } else {
        segment().track(`Gallery Image Clicked - ${gallery.caption}`, {gallery: gallery})
        const nextIndex = activeImageIndex + 1 >= work.length ? 0 : activeImageIndex + 1
        setActiveImageIndex(nextIndex)
        moveNext()
      }
    }}
  />
  )
}
